





























import { Component, Vue, Prop, Watch } from 'vue-property-decorator'
import VueHtml2pdf from 'vue-html2pdf'
Vue.use(VueHtml2pdf)

@Component({
  components: {
  }
})

export default class Html2pdf extends Vue {
@Prop() htmlContent!: string;
@Prop() config!: any;

private browser = {
  versions: (function() {
    var u = window.navigator.userAgent
    var app = window.navigator.appVersion
    return {
      trident: u.indexOf('Trident') > -1, // IE
      presto: u.indexOf('Presto') > -1, // opera
      webKit: u.indexOf('AppleWebKit') > -1, // apple google
      gecko: u.indexOf('Gecko') > -1 && u.indexOf('KHTML') === -1, // firefox
      mobile: !!u.match(/AppleWebKit.*Mobile.*/),
      ios: !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/), // ios
      android: u.indexOf('Android') > -1 || u.indexOf('Adr') > -1, // android
      iPhone: u.indexOf('iPhone') > -1, // iPhone
      iPad: u.indexOf('iPad') > -1, // iPad
      webApp: u.indexOf('Safari') === -1
    }
  })(),
  language: navigator.language.toLowerCase()
};

  mounted()
  {
    if (!this.browser.versions.iPhone && !this.browser.versions.android) {
      this.config.download = true
      this.config.preview=true
    } else {
       this.config.download = true
      this.config.preview=false
    }
  }

  @Watch("progress")
  onProgress (val:number) {
      this.$emit('progress', val)
  }

  generatePdf () {
      let h2p = this.$refs.html2Pdf as any
      h2p.generatePdf()
  }

}
